import React, { Component } from "react";
import styles from "./register.module.scss";
import Button from "./../../components/Button/Button.js";
import Input from "./../../components/Input/Input";
import { validateField as validateFiel } from "./../../components/Validate/Validate";

import api from "./../../services/api";

export default class Register extends Component {
  /* Constructor */
  constructor(props) {
    super(props);

    this.state = {
      errors: {
        name: false,
        email: false,
        repeatedEmail: false
      },
      logged: false
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.validateFieldddd = this.validateFieldddd.bind(this);
  }

  /* Functions */
  handleClick = e => {
    e.preventDefault();

    const bodyRequest = {
      name: this.inputUserName.value,
      email: this.userEmail.value,
      requestInfo: true
    };

    api.postUser(bodyRequest).then(data => {
      window.call = data;
    });
    console.log(window.call);
  };
  handleInputChange = e => {
    //this.validateField(e.target.value, e);

    validateFiel(e);
  };

  validateFieldddd = (value, e) => {
    let regex;
    switch (e.target.name) {
      case "email":
        regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        break;
      case "":
        regex = "";
    }
    //console.log(this.userEmail.value);
    if (regex.test(value)) {
      this.userEmail.setState({
        error: false,
        valid: true
      });
    } else {
      // invalid email, maybe show an error to the user.
      this.userEmail.setState({
        error: true,
        valid: false
      });
    }
  };

  render() {
    /* Return */
    return (
      <div className={styles.mainWrapper}>
        {/*--- LEFT WRAPPER ---*/}
        <div className={styles.leftWrapper}>
          <div className={styles.leftChild}>
            {/* ---------- FORM ----------*/}
            <div className={styles.registerForm}>
              <form onSubmit={this.handleClick}>
                <p>
                  Accede a tu cuenta para subir o actualizar tus cartas y seguir
                  por dónde viajan ahora mismo.
                </p>

                {/* User name */}

                <Input
                  name="user-name"
                  type="text"
                  placeholder="Escribe tu nombre"
                  labelText="Escribe tu nombre"
                  error={false}
                  ref={inputElement => (this.inputUserName = inputElement)}
                  errorMsg="Username or Password is incorrect."
                />

                {/* User email  */}
                <Input
                  name="email"
                  fieldType="email"
                  type="text"
                  placeholder="Escribe tu email"
                  labelText="Escribe tu email"
                  error={false}
                  ref={inputElement => (this.userEmail = inputElement)}
                  errorMsg="Username or Password is incorrect."
                  onChange={this.handleInputChange}
                />

                {/* User repeated email  */}
                <Input
                  name="user-repeated-mail"
                  type="text"
                  placeholder="Repite tu email"
                  labelText="Repite tu email"
                  error={false}
                  ref={inputElement =>
                    (this.inputUserRepeatedEmail = inputElement)
                  }
                  errorMsg="Username or Password is incorrect."
                />

                {/* Button */}
                <Button onClick={this.handleClick} text="Regístrate" />
              </form>
            </div>
          </div>
        </div>
        <div className={styles.rightWrapper}>
          <div className={styles.rightChild} />
        </div>
      </div>
    );
  }
}
