import React, { Component } from "react";
import styles from "./input.module.scss";
export default class Input extends Component {
  /* Constructor */
  constructor(props) {
    super(props);

    this.state = {
      active: false,
      error: this.props.error,
      valid: this.props.valid
    };
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange = e => {
    e.target.value.length > 0
      ? (this.setState.active = true)
      : (this.setState.active = false);
  };

  render() {
    let errorClass = this.state.error
      ? styles.inputError
      : this.state.valid
        ? styles.inputValid
        : null;
    let activeClass = this.state.active ? styles.inputActive : null;

    return (
      <div className={styles.inputWrapper}>
        <input
          className={`${styles.input} ${errorClass} ${activeClass}`}
          type={this.props.type}
          name={this.props.name}
          placeholder={this.props.placeholder}
          ref={inputElement => (this[this.props.refId] = inputElement)}
          value={this.props.value}
          onChange={this.props.onChange}
        />
        <label htmlFor={this.props.name}>{this.props.labelText}</label>
        <p className={styles.errorMsg}>{this.props.errorMsg}</p>
      </div>
    );
  }
}
